import React from 'react'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Icon from '@material-ui/core/Icon';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import {Draggable} from "react-beautiful-dnd";
import styled from "styled-components";
import { connect } from "react-redux";
import { deleteCard, editCard } from "../actions";
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import TextArea from 'react-textarea-autosize';
import { useDispatch } from 'react-redux'


const CardContainer = styled.div`
  margin-bottom: .5em;
`


class TrelloCard extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      message: this.props.message,
      showEdit: "none",
      showText: "inline"
    }

    this.deleteCard = this.deleteCard.bind(this);
    this.openEdit = this.openEdit.bind(this);
    this.closeTextArea = this.closeTextArea.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
  }
  
// const TrelloCard = ({id, message, index, list}) => {
  deleteCard() {
    console.log("Delete card button pressed");
    // const { dispatch } = this.props;
    return;
    // dispatch(deleteCard(this.props.list, this.props.index));
    
  }

  handleInputChange = (e) => {
    this.setState({
      message: e.target.value
    })
    const { dispatch } = this.props;
    dispatch(editCard(this.props.list, this.props.id, this.state.message));

  }

  closeTextArea() {
    this.setState({
      showText: "inline",
      showEdit: "none"
    })
    const { dispatch } = this.props;
    dispatch(editCard(this.props.list, this.props.id, this.state.message));
  }

  openEdit() {
    this.setState({
      showText: "none",
      showEdit: "inline"
    })
  }


  render () {
  
  return (
    <Draggable draggableId={String(this.props.id)} index={this.props.index}>
      { (provided) => (
        <CardContainer ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
        <Card key={this.props.id} sx={{ maxWidth: 300 }}>
        <CardContent style={{display: "flex"}}>
          <Typography id="" sx={{ fontSize: 14 }} style={{display:this.state.showText}} color="text.secondary" gutterBottom>
            {this.props.message}
          </Typography>
          <TextArea
                  autoFocus 
                  onBlur={this.closeTextArea}
                  value={this.state.message} 
                  onChange={this.handleInputChange}
                  style={{
                    display: this.state.showEdit,
                    resize: "none",
                    width: "100%",
                    outline: "1px",
                    border: "1px",
                    
                  }} />

            
          
          <div style={{marginLeft: "auto", display: "flex", flexDirection: "column"}}>
          < AlertDialog style={{marginLeft: "auto"}} list={this.props.list} index={this.props.index} yesFunction={this.deleteCard}/>
          <div>
          <Icon fontSize='small' style={{marginLeft: 8, cursor: "pointer", color: "black"}} onClick={this.openEdit}>
          edit
          </Icon>
          </div>
          </div>
          
        </CardContent>
        <Button variant="contained" 
                style={{color: "white", backgroundColor:"#5aac44", display: this.state.showEdit, marginLeft: 8, marginBottom: 8}}
                onMouseDown={this.closeTextArea}>
                Finish Editing
          </Button>
        </Card>
        </CardContainer>
        
      )}
    </Draggable>
    )
  }
}

function EditDialog(props) {
  const [open, setOpen] = React.useState(false);
  return (
    <div>
    <Icon fontSize='small' style={{marginLeft: 8, cursor: "pointer", color: "black"}} onClick>
    edit
    </Icon>
    </div>
  );
}

function AlertDialog(props) {
  const [open, setOpen] = React.useState(false);
  const list = props.list;
  const index = props.index;
  const localFunction = props.deleteCard;
  const dispatch = useDispatch();
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    
    setOpen(false);
  };

  const handleConfirm = () => {
    console.log("User has confirmed deletion of", list, "index", index);
    dispatch(deleteCard(list, index));
    setOpen(false);
  }



  return (
    <div >
      <Icon fontSize='small' style={{marginLeft: 8, cursor: "pointer", color: "red"}} onClick={handleClickOpen}>
        close
      </Icon>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Delete this task?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure you want to delete this task? This cannot be undone.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>No</Button>
          <Button key="yes" onClick={handleConfirm} autoFocus>
            Yes
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default connect() (TrelloCard);